<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="index">
        <ToolBar>
            <el-button type="primary" icon="el-icon-plus" size="small">新增轮播图</el-button>
            <div>
                <el-input
                        placeholder="请输入轮播图标题"
                        size="small"
                        style="width: 140px"
                        v-model="searchParams.postTitle"
                        clearable>
                </el-input>
                <el-select v-model="searchParams.postType" size="small" clearable placeholder="选择投放的频道"
                           style="width: 140px">
                    <el-option
                            v-for="(v,k) in $Cfg.postType"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>

                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="选择导航分类"
                           style="width: 120px">
                    <el-option
                            v-for="(v,k) in $Cfg.postStatus"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>
                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="选择投放位置"
                           style="width: 120px">
                    <el-option
                        v-for="(v,k) in $Cfg.postStatus"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>
                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="状态"
                           style="width: 120px">
                    <el-option
                        v-for="(v,k) in $Cfg.postStatus"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>
                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="线上状态"
                           style="width: 120px">
                    <el-option
                        v-for="(v,k) in $Cfg.postStatus"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>
                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="操作人"
                           style="width: 120px">
                    <el-option
                        v-for="(v,k) in $Cfg.postStatus"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>
                <el-button type="success" size="small" @click="">查询</el-button>
                <el-button type="warning" size="small" @click="clearSearchParams()">重置</el-button>
            </div>
        </ToolBar>
        <el-table
                :data="tableData"
                border
                style="width: 100%">
            <el-table-column
                    prop="date"
                    label="ID"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="轮播图标题"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="address"
                    label="轮播图投放位置">
            </el-table-column>
            <el-table-column
                prop="address"
                label="链接地址">
            </el-table-column>
            <el-table-column
                prop="address"
                label="起始时间">
            </el-table-column>
            <el-table-column
                prop="address"
                label="轮播顺序">
            </el-table-column>
            <el-table-column
                prop="address"
                label="状态">
            </el-table-column>
            <el-table-column
                prop="address"
                label="线上状态">
            </el-table-column>
            <el-table-column
                prop="address"
                label="创建时间">
            </el-table-column>
            <el-table-column
                    label="操作"
                    width="216">
                <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="danger" size="small"
                        >禁用</el-button>
                    <el-button @click="handleClick(scope.row)" type="success" size="small"
                        >预览</el-button>
                    <el-button @click="$router.push('/carousel/carousel_list/carousel_edit')" type="primary" size="small"
                        >修改</el-button>
                </template>
            </el-table-column>
        </el-table>
        <Pagination
            :params="searchParams"
            :requestFunc="requestFunc"
            @returnData="returnData"
        />
    </div>
</template>

<script>
    import ToolBar from '@/components/ToolBar.vue';
    import HelpHint from '@/components/HelpHint.vue';
    import Pagination from '@/components/Pagination.vue';
    import paginationMixin from './mixin/page.js'

    export default {
        mixins:[paginationMixin],
        data() {
            return {
                tableData: [{
                    date: '2016-05-03',
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }, {
                    date: '2016-05-02',
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }]
            }
        },
        methods: {
            handleClick(row) {
                this.$alert(row, '标题名称', {
                    confirmButtonText: '确定',
                    callback: action => {}
                });
            },


        },
        created() {

        },
        components: {
            ToolBar, HelpHint, Pagination
        }
    }
</script>
